import React from "react";
import ReactDOM from "react-dom";
import { List, Badge } from "antd-mobile";
import { TabBar, Icon } from "antd-mobile";


export class TabBarExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: "red",
      hidden: false
    };
  }

  renderContent(pageText) {
    return (
      <div
        style={{
          backgroundColor: "white",
          height: "100%",
          textAlign: "center"
        }}
      >
        <div style={{ paddingTop: 60 }}>
          {pageText} tab {pageText}
        </div>
        <a
          style={{
            display: "block",
            marginTop: 40,
            marginBottom: 600,
            color: "#108ee9"
          }}
          onClick={e => {
            e.preventDefault();
            this.setState({
              hidden: !this.state.hidden
            });
          }}
        >
           tab-bar /
        </a>
      </div>
    );
  }

  render() {
    return (
      <TabBar
        unselectedTintColor="#949494"
        tintColor="#33A3F4"
        barTintColor="white"
        hidden={this.state.hidden}
      >
        <TabBar.Item
          title=""
          key=""
          icon={
            <div
              style={{
                width: "0.44rem",
                height: "0.44rem",
                background:
                  "url(https://zos.alipayobjects.com/rmsportal/sifuoDUQdAFKAVcFGROC.svg) center center /  0.42rem 0.42rem no-repeat"
              }}
            />
          }
          selectedIcon={
            <div
              style={{
                width: "0.44rem",
                height: "0.44rem",
                background:
                  "url(https://zos.alipayobjects.com/rmsportal/iSrlOTqrKddqbOmlvUfq.svg) center center /  0.42rem 0.42rem no-repeat"
              }}
            />
          }
          selected={this.state.selectedTab === "blueTab"}
          badge={1}
          onPress={() => {
            this.setState({
              selectedTab: "blueTab"
            });
          }}
          data-seed="logId"
        >
          
        </TabBar.Item>

        <TabBar.Item
          icon={
            <div
              style={{
                width: "0.44rem",
                height: "0.44rem",
                background:
                  "url(https://zos.alipayobjects.com/rmsportal/psUFoAMjkCcjqtUCNPxB.svg) center center /  0.42rem 0.42rem no-repeat"
              }}
            />
          }
          selectedIcon={
            <div
              style={{
                width: "0.44rem",
                height: "0.44rem",
                background:
                  "url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  0.42rem 0.42rem no-repeat"
              }}
            />
          }
          title=""
          key=""
          dot
          selected={this.state.selectedTab === "greenTab"}
          onPress={() => {
            this.setState({
              selectedTab: "greenTab"
            });
          }}
        >
          {this.renderContent("")}
        </TabBar.Item>
        <TabBar.Item
          icon={{
            uri:
              "https://zos.alipayobjects.com/rmsportal/asJMfBrNqpMMlVpeInPQ.svg"
          }}
          selectedIcon={{
            uri:
              "https://zos.alipayobjects.com/rmsportal/gjpzzcrPMkhfEqgbYvmN.svg"
          }}
          title=""
          key=""
          selected={this.state.selectedTab === "yellowTab"}
          onPress={() => {
            this.setState({
              selectedTab: "yellowTab"
            });
          }}
        >
          {this.renderContent("")}
        </TabBar.Item>
      </TabBar>
    );
  }
}



